    	img_url1_s:'',//1
    	
    	img_url2:'static/img/imgjia.png',
    	inp1_show2:true,
    	img_url2_s:'',//2
    	
    	x_name:"",//
    	stage_name:'',//
    	age:'',//
    	sexa:0,//     01
    	x_height:'',//
    	Three_dimensions:'',//
    	wxid:'',//
    	dyid:'',//
    	vb_name:'',//
    	x_iphone:'',//
    	Current_city:'',//
    	graduated_school:'',//
    	interest:'',//
    	Personal_profile:'',//
    	
    	gou_show:false,//false
    	
    }
  },
  computed: {
