               front_pic:this.img_url1_s,
               cover_pic:this.img_url2_s,
               url:this.video_id,
               tik_tok:this.dyid,
               weibo:this.vb_name,
               speciality:this.interest,
               site:this.Personal_profile
             }
            }).then(res=>{
            	     console.log(res.data);
            	    if(res.data.code = 200){
        	    	 	   this.$toast.success({
