        	                 });
             });
  		  	}else{
  		  		 this.$toast({message:'',duration:2000});
  		  	}
  		  }else{this.$toast({message:'',duration:1000})}
  	},
  	
  //
  PostFile(file,i,num){
    var name = file.name,             //
      size = file.size,             //shardSize = 2 * 1024 * 1024,
      shardSize = 1024*512,        //?MB,
      shardCount = Math.ceil(size / shardSize); //
            console.log(shardCount,'')
    if(i > shardCount){
      return;
    }
    var start = i * shardSize;
    var end = start + shardSize;
        var packet = file.slice(start,end)
        if((i+1) > shardCount){
        	//
//      	var fd = {'filename':name,'chunks':shardCount,'act':'combine'},
        	 $.post(
            'http://store.kuntopic.com/upload-file',
            {"act":"combine","chunks":shardCount,"filename":name},
             e=>{
                if(e.flag){
                	  console.log(e,'',num); 
                	 if(num==1){
                	 	  this.img_url1_ss = e.url;
                	 	  this.img_id3 = e.url;
  	 	                this.inp1_show_s = false;
                	 }else if(num==2){
                	 	  this.img_url1 = e.url;
  	 	                this.inp1_show = false;
  	 	                this.img_url1_s = e.url
                	 }else if(num==3){
                	 	  this.img_url2 = e.url;
  	 	                this.inp1_show2 = false;
  	 	                this.img_url2_s = e.url
                	 }else if(num==4){
                	 	 this.$refs.video.src = e.url;
                	   this.video_show = false;
                	   this.videoimg = 'static/img/banner.928342a.png'
                	   this.video_id = e.url;
                	 }
                	  window.setTimeout(()=>{this.login_nub = 0;},1000);
                    this.login_nub = 100;
                    this.login = false;
                    Notify({
                     message: '',
                     duration: 1000,
                     background: '#1989fa'
                   });
                }
             },'json'
           );
          
        }else{
        	 var fd = new FormData();
       fd.append("newfile", packet); //slice
       fd.append("filename", name);
       fd.append("chunks", shardCount); //
           fd.append("chunk", i); //
        
    axios({
      url:"http://store.kuntopic.com/upload-file",
      method:"post",
      data: fd,
            cache: false,
      //timeout:"10000",
      async: true, //
      dataType:"json",
      processData: false, //jqueryform
      contentType: false, //falseContent-Type
      }).then(res=>{
        console.log(res);
        /*  */
        if (res.data.code == 'wait') {
          fd = '';
          i++;
                    var su = 100/(shardCount+1);
                    this.login_nub = i*su;
          this.PostFile(file,i,num);
        
        } else {
          console.log('');
        }
      }).catch(err=>{ console.log(err)})
	       }
 
  },

  	
  
