    if (!files.length) return;
    //
    console.log(files,'')
       
        let size = files[0].size/1024;
       console.log(size,'  ')
       if(size>=100000){
       	    this.$toast({message:'100M',duration:2000}); 
       }else{
       	        this.login = true;
       	        this.login_nub = 0;
       	        
       	        this.PostFile(files[0],0,4);
       	        
//              this.go_cideo(files[0]);
       	        
       	      
       	      	
                //
//       	 var reader = new FileReader();
//          	 this.file = files[0];
//            	 var _this = this;
//            	 
//          	 reader.onload = function(){
//           	  _this.$refs.video.src = this.result;
//          	 };
//          	 reader.readAsDataURL(this.file);
//            	  
//               this.creatImg(files);
       	      
          }  
  	},
  	
  	
  	x_img(i){//
  		if(i==1){
  			  this.img_url1 = 'static/img/imgjia.png';this.inp1_show = true;this.img_url1_s='';
  		}else if(i==2){
